<script>
import Hint from "@/components/Hint.vue";

export default {
  data() {
    return {
      code: Number,
      activeHint: "",
      wrongCode: false,
      rightCode: false,
      options: {
        afterLoad: this.afterLoad,
        sectionsColor: ["#1f1f1f", "#ff5f45", "#0798ec"],
      },
    };
  },
  methods: {
    afterLoad() {
      console.log("Emitted 'after load' event.");
    },
    showHint(index) {
      this.activeHint = index;
      console.log(index, this.activeHint);
    },
    checkCode() {
      console.log(this.code);
      if (this.code.length == 4) {
        if (this.code == "7023") {
          this.rightCode = true;
        } else {
          this.wrongCode = true;
          setTimeout(() => {
            this.code = "";
          }, 1000);
          setTimeout(() => {
            this.wrongCode = false;
          }, 1000);
        }
      }
    },
  },
  components: {
    Hint,
  },
};
</script>

